import {Renderer} from "./renderer";

let personPos = {
	'c': 0,
	'r': 0,
};

let targetPos = {};

let renderer = new Renderer(1401, 641);

document.addEventListener('DOMContentLoaded', () => {
	renderer.drawGrid();
	renderer.drawPlayer(personPos.c, personPos.r);
	renderer.drawTrees();
});

let path = [];
let start = personPos;
let animationFrame = null;
let timerId = null;

let old = {};

renderer.get().addEventListener( 'click', (e) => {
	animationFrame = null;
	clearInterval(timerId);

	let target = { c: renderer.getColFromX(e.clientX), r: renderer.getRowFromY(e.clientY) };

	for (let i = 0; i < path.length; i++) {
		console.log('test');
		let res = path[i].split(',');
		renderer.context.clearRect(renderer.getXFromCol(res[0]) + 1, renderer.getYFromRow(res[1]) + 1, 38, 38);
	}

	let path = new PathFinder();
	path = path.generatePath(start, target);

	if(!path) {
		return
	}

	renderer.drawPath(path);

	old = {
		c: start.c,
		r: start.r
	};

	animationFrame = window.requestAnimationFrame(animatePath);
});

function animatePath() {
	let curr = path.shift();

	if(!curr){
		window.cancelAnimationFrame(animationFrame);
		return
	}

	renderer.context.clearRect(renderer.getXFromCol(old.c) + 1, renderer.getYFromRow(old.r) + 1, 38, 38);
	let res = curr.split(',');
	start = {c: res[0], r: res[1]};
	old = {
		c: res[0],
		r: res[1]
	};
	renderer.context.clearRect(renderer.getXFromCol(old.c) + 1, renderer.getYFromRow(old.r) + 1, 38, 38);
	renderer.drawPlayer(res[0], res[1]);

	if (path.length > 0) {
		timerId = setTimeout(() => {
			window.requestAnimationFrame(animatePath);
		}, 200)
	}
}